  .blob {
    position: absolute;
    top:120%;
    left: 20px;
    fill: #023F92;
    width: 20vmax;
    z-index: 1;
    animation: move 20s ease-in-out infinite;
    transform-origin: 50% 50%;
    background-color:linear-gradient(145deg, #ff2b2b, #BB1616);
  }
  
  @keyframes move {
    0%   { transform: scale(2.5)   translate(10px, -17px); }
    38%  { transform: scale(2.8, 2.3) translate(8vw, 21vh) rotate(160deg); }
    40%  { transform: scale(2.8, 2.6) translate(8vw, 20vh) rotate(160deg); }
    78%  { transform: scale(2.7) translate(0vw, 22vh) rotate(-20deg); }
    80%  { transform: scale(2.7) translate(0vw, 21vh) rotate(-20deg); }
    100% { transform: scale(2)   translate(5px, -14px); }
  }
